"use client";

import React, { createContext, useContext, useState, useEffect } from 'react';
import { Project, Expense, ProjectSummary } from '@/types';

interface ProjectContextType {
  projects: Project[];
  addProject: (project: Omit<Project, 'id' | 'expenses'>) => void;
  updateProject: (id: string, project: Partial<Project>) => void;
  deleteProject: (id: string) => void;
  addExpense: (projectId: string, expense: Omit<Expense, 'id' | 'projectId'>) => void;
  updateExpense: (projectId: string, expenseId: string, expense: Partial<Expense>) => void;
  deleteExpense: (projectId: string, expenseId: string) => void;
  getProjectSummaries: () => ProjectSummary[];
  getProjectById: (id: string) => Project | undefined;
}

const ProjectContext = createContext<ProjectContextType | undefined>(undefined);

export function ProjectProvider({ children }: { children: React.ReactNode }) {
  const [projects, setProjects] = useState<Project[]>([]);

  // Load data from localStorage on mount
  useEffect(() => {
    const savedProjects = localStorage.getItem('projects');
    if (savedProjects) {
      setProjects(JSON.parse(savedProjects));
    }
  }, []);

  // Save to localStorage whenever projects change
  useEffect(() => {
    localStorage.setItem('projects', JSON.stringify(projects));
  }, [projects]);

  const addProject = (projectData: Omit<Project, 'id' | 'expenses'>) => {
    const newProject: Project = {
      ...projectData,
      id: Date.now().toString(),
      expenses: []
    };
    setProjects(prev => [...prev, newProject]);
  };

  const updateProject = (id: string, projectData: Partial<Project>) => {
    setProjects(prev => prev.map(project => 
      project.id === id ? { ...project, ...projectData } : project
    ));
  };

  const deleteProject = (id: string) => {
    setProjects(prev => prev.filter(project => project.id !== id));
  };

  const addExpense = (projectId: string, expenseData: Omit<Expense, 'id' | 'projectId'>) => {
    const newExpense: Expense = {
      ...expenseData,
      id: Date.now().toString(),
      projectId
    };

    setProjects(prev => prev.map(project => 
      project.id === projectId 
        ? { ...project, expenses: [...project.expenses, newExpense] }
        : project
    ));
  };

  const updateExpense = (projectId: string, expenseId: string, expenseData: Partial<Expense>) => {
    setProjects(prev => prev.map(project => 
      project.id === projectId 
        ? {
            ...project,
            expenses: project.expenses.map(expense =>
              expense.id === expenseId ? { ...expense, ...expenseData } : expense
            )
          }
        : project
    ));
  };

  const deleteExpense = (projectId: string, expenseId: string) => {
    setProjects(prev => prev.map(project => 
      project.id === projectId 
        ? {
            ...project,
            expenses: project.expenses.filter(expense => expense.id !== expenseId)
          }
        : project
    ));
  };

  const getProjectSummaries = (): ProjectSummary[] => {
    return projects.map(project => ({
      id: project.id,
      sot: project.sot,
      cliente: project.cliente,
      fecha: project.fecha,
      lugar: project.lugar,
      montoTotal: project.expenses.reduce((total, expense) => {
        if (expense.tipo === 'viaticos') {
          const viaticos = expense.detalles as any;
          const alimentoTotal = Object.values(viaticos.alimento || {}).reduce((sum: number, val: any) => sum + (val || 0), 0);
          return total + (viaticos.hospedaje || 0) + alimentoTotal;
        }
        return total + expense.monto;
      }, 0)
    }));
  };

  const getProjectById = (id: string): Project | undefined => {
    return projects.find(project => project.id === id);
  };

  return (
    <ProjectContext.Provider value={{
      projects,
      addProject,
      updateProject,
      deleteProject,
      addExpense,
      updateExpense,
      deleteExpense,
      getProjectSummaries,
      getProjectById
    }}>
      {children}
    </ProjectContext.Provider>
  );
}

export function useProjects() {
  const context = useContext(ProjectContext);
  if (context === undefined) {
    throw new Error('useProjects must be used within a ProjectProvider');
  }
  return context;
}
