"use client";

import { useState } from 'react';
import { useProjects } from '@/context/ProjectContext';
import { Project, Expense } from '@/types';
import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Textarea } from '@/components/ui/textarea';
import { Separator } from '@/components/ui/separator';
import { Badge } from '@/components/ui/badge';
import { Trash2, Edit, Plus } from 'lucide-react';

export default function ProjectRegistration() {
  const { projects, addProject, updateProject, deleteProject, addExpense, updateExpense, deleteExpense } = useProjects();
  const [selectedProject, setSelectedProject] = useState<string>('');
  const [editingProject, setEditingProject] = useState<string | null>(null);
  const [editingExpense, setEditingExpense] = useState<string | null>(null);

  // Project form state
  const [projectForm, setProjectForm] = useState({
    sot: '',
    cliente: '',
    personal: '',
    lugar: '',
    fecha: ''
  });

  // Expense form state
  const [expenseForm, setExpenseForm] = useState({
    tipo: '' as 'movilidad' | 'viaticos' | 'compras',
    monto: 0,
    // Movilidad
    inicio: '',
    destino: '',
    // Viáticos
    hospedaje: 0,
    desayuno: 0,
    almuerzo: 0,
    cena: 0,
    // Compras
    descripcion: ''
  });

  const resetProjectForm = () => {
    setProjectForm({
      sot: '',
      cliente: '',
      personal: '',
      lugar: '',
      fecha: ''
    });
    setEditingProject(null);
  };

  const resetExpenseForm = () => {
    setExpenseForm({
      tipo: '' as any,
      monto: 0,
      inicio: '',
      destino: '',
      hospedaje: 0,
      desayuno: 0,
      almuerzo: 0,
      cena: 0,
      descripcion: ''
    });
    setEditingExpense(null);
  };

  const handleProjectSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (editingProject) {
      updateProject(editingProject, projectForm);
    } else {
      addProject(projectForm);
    }
    resetProjectForm();
  };

  const handleExpenseSubmit = (e: React.FormEvent) => {
    e.preventDefault();
    if (!selectedProject) return;

    let detalles: any;
    let monto = expenseForm.monto;

    switch (expenseForm.tipo) {
      case 'movilidad':
        detalles = {
          inicio: expenseForm.inicio,
          destino: expenseForm.destino
        };
        break;
      case 'viaticos':
        detalles = {
          hospedaje: expenseForm.hospedaje,
          alimento: {
            desayuno: expenseForm.desayuno || undefined,
            almuerzo: expenseForm.almuerzo || undefined,
            cena: expenseForm.cena || undefined
          }
        };
        monto = expenseForm.hospedaje + expenseForm.desayuno + expenseForm.almuerzo + expenseForm.cena;
        break;
      case 'compras':
        detalles = {
          descripcion: expenseForm.descripcion
        };
        break;
    }

    const expense: Omit<Expense, 'id' | 'projectId'> = {
      tipo: expenseForm.tipo,
      monto,
      detalles
    };

    if (editingExpense) {
      updateExpense(selectedProject, editingExpense, expense);
    } else {
      addExpense(selectedProject, expense);
    }
    resetExpenseForm();
  };

  const handleEditProject = (project: Project) => {
    setProjectForm({
      sot: project.sot,
      cliente: project.cliente,
      personal: project.personal,
      lugar: project.lugar,
      fecha: project.fecha
    });
    setEditingProject(project.id);
  };

  const handleEditExpense = (expense: Expense) => {
    const resetForm = {
      tipo: expense.tipo,
      monto: expense.monto,
      inicio: '',
      destino: '',
      hospedaje: 0,
      desayuno: 0,
      almuerzo: 0,
      cena: 0,
      descripcion: ''
    };

    if (expense.tipo === 'movilidad') {
      const details = expense.detalles as any;
      resetForm.inicio = details.inicio;
      resetForm.destino = details.destino;
    } else if (expense.tipo === 'viaticos') {
      const details = expense.detalles as any;
      resetForm.hospedaje = details.hospedaje || 0;
      resetForm.desayuno = details.alimento?.desayuno || 0;
      resetForm.almuerzo = details.alimento?.almuerzo || 0;
      resetForm.cena = details.alimento?.cena || 0;
    } else if (expense.tipo === 'compras') {
      const details = expense.detalles as any;
      resetForm.descripcion = details.descripcion;
    }

    setExpenseForm(resetForm);
    setEditingExpense(expense.id);
  };

  const selectedProjectData = projects.find(p => p.id === selectedProject);

  return (
    <div className="space-y-6">
      <Tabs defaultValue="projects" className="w-full">
        <TabsList className="grid w-full grid-cols-2">
          <TabsTrigger value="projects">Proyectos</TabsTrigger>
          <TabsTrigger value="expenses">Gastos</TabsTrigger>
        </TabsList>

        <TabsContent value="projects" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>{editingProject ? 'Editar Proyecto' : 'Nuevo Proyecto'}</CardTitle>
            </CardHeader>
            <CardContent>
              <form onSubmit={handleProjectSubmit} className="space-y-4">
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="sot">SOT</Label>
                    <Input
                      id="sot"
                      value={projectForm.sot}
                      onChange={(e) => setProjectForm(prev => ({ ...prev, sot: e.target.value }))}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="cliente">Cliente</Label>
                    <Input
                      id="cliente"
                      value={projectForm.cliente}
                      onChange={(e) => setProjectForm(prev => ({ ...prev, cliente: e.target.value }))}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="personal">Personal</Label>
                    <Input
                      id="personal"
                      value={projectForm.personal}
                      onChange={(e) => setProjectForm(prev => ({ ...prev, personal: e.target.value }))}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="lugar">Lugar</Label>
                    <Input
                      id="lugar"
                      value={projectForm.lugar}
                      onChange={(e) => setProjectForm(prev => ({ ...prev, lugar: e.target.value }))}
                      required
                    />
                  </div>
                  <div>
                    <Label htmlFor="fecha">Fecha</Label>
                    <Input
                      id="fecha"
                      type="date"
                      value={projectForm.fecha}
                      onChange={(e) => setProjectForm(prev => ({ ...prev, fecha: e.target.value }))}
                      required
                    />
                  </div>
                </div>
                <div className="flex gap-2">
                  <Button type="submit">
                    {editingProject ? 'Actualizar' : 'Crear'} Proyecto
                  </Button>
                  {editingProject && (
                    <Button type="button" variant="outline" onClick={resetProjectForm}>
                      Cancelar
                    </Button>
                  )}
                </div>
              </form>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Proyectos Existentes</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="space-y-2">
                {projects.map((project) => (
                  <div key={project.id} className="flex items-center justify-between p-3 border rounded-lg">
                    <div>
                      <div className="font-medium">{project.sot} - {project.cliente}</div>
                      <div className="text-sm text-muted-foreground">
                        {project.lugar} | {project.fecha} | {project.personal}
                      </div>
                    </div>
                    <div className="flex gap-2">
                      <Button
                        size="sm"
                        variant="outline"
                        onClick={() => handleEditProject(project)}
                      >
                        <Edit className="h-4 w-4" />
                      </Button>
                      <Button
                        size="sm"
                        variant="destructive"
                        onClick={() => deleteProject(project.id)}
                      >
                        <Trash2 className="h-4 w-4" />
                      </Button>
                    </div>
                  </div>
                ))}
              </div>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="expenses" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Seleccionar Proyecto</CardTitle>
            </CardHeader>
            <CardContent>
              <Select value={selectedProject} onValueChange={setSelectedProject}>
                <SelectTrigger>
                  <SelectValue placeholder="Selecciona un proyecto" />
                </SelectTrigger>
                <SelectContent>
                  {projects.map((project) => (
                    <SelectItem key={project.id} value={project.id}>
                      {project.sot} - {project.cliente}
                    </SelectItem>
                  ))}
                </SelectContent>
              </Select>
            </CardContent>
          </Card>

          {selectedProject && (
            <>
              <Card>
                <CardHeader>
                  <CardTitle>{editingExpense ? 'Editar Gasto' : 'Nuevo Gasto'}</CardTitle>
                </CardHeader>
                <CardContent>
                  <form onSubmit={handleExpenseSubmit} className="space-y-4">
                    <div>
                      <Label htmlFor="tipo">Tipo de Gasto</Label>
                      <Select
                        value={expenseForm.tipo}
                        onValueChange={(value: 'movilidad' | 'viaticos' | 'compras') => 
                          setExpenseForm(prev => ({ ...prev, tipo: value }))
                        }
                      >
                        <SelectTrigger>
                          <SelectValue placeholder="Selecciona el tipo" />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="movilidad">Movilidad</SelectItem>
                          <SelectItem value="viaticos">Viáticos</SelectItem>
                          <SelectItem value="compras">Compras</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>

                    {expenseForm.tipo === 'movilidad' && (
                      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                        <div>
                          <Label htmlFor="inicio">Inicio</Label>
                          <Input
                            id="inicio"
                            value={expenseForm.inicio}
                            onChange={(e) => setExpenseForm(prev => ({ ...prev, inicio: e.target.value }))}
                            required
                          />
                        </div>
                        <div>
                          <Label htmlFor="destino">Destino</Label>
                          <Input
                            id="destino"
                            value={expenseForm.destino}
                            onChange={(e) => setExpenseForm(prev => ({ ...prev, destino: e.target.value }))}
                            required
                          />
                        </div>
                        <div>
                          <Label htmlFor="monto">Monto</Label>
                          <Input
                            id="monto"
                            type="number"
                            step="0.01"
                            value={expenseForm.monto}
                            onChange={(e) => setExpenseForm(prev => ({ ...prev, monto: parseFloat(e.target.value) || 0 }))}
                            required
                          />
                        </div>
                      </div>
                    )}

                    {expenseForm.tipo === 'viaticos' && (
                      <div className="space-y-4">
                        <div>
                          <Label htmlFor="hospedaje">Hospedaje</Label>
                          <Input
                            id="hospedaje"
                            type="number"
                            step="0.01"
                            value={expenseForm.hospedaje}
                            onChange={(e) => setExpenseForm(prev => ({ ...prev, hospedaje: parseFloat(e.target.value) || 0 }))}
                          />
                        </div>
                        <div>
                          <Label>Alimentos</Label>
                          <div className="grid grid-cols-1 md:grid-cols-3 gap-4 mt-2">
                            <div>
                              <Label htmlFor="desayuno">Desayuno</Label>
                              <Input
                                id="desayuno"
                                type="number"
                                step="0.01"
                                value={expenseForm.desayuno}
                                onChange={(e) => setExpenseForm(prev => ({ ...prev, desayuno: parseFloat(e.target.value) || 0 }))}
                              />
                            </div>
                            <div>
                              <Label htmlFor="almuerzo">Almuerzo</Label>
                              <Input
                                id="almuerzo"
                                type="number"
                                step="0.01"
                                value={expenseForm.almuerzo}
                                onChange={(e) => setExpenseForm(prev => ({ ...prev, almuerzo: parseFloat(e.target.value) || 0 }))}
                              />
                            </div>
                            <div>
                              <Label htmlFor="cena">Cena</Label>
                              <Input
                                id="cena"
                                type="number"
                                step="0.01"
                                value={expenseForm.cena}
                                onChange={(e) => setExpenseForm(prev => ({ ...prev, cena: parseFloat(e.target.value) || 0 }))}
                              />
                            </div>
                          </div>
                        </div>
                      </div>
                    )}

                    {expenseForm.tipo === 'compras' && (
                      <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        <div>
                          <Label htmlFor="descripcion">Descripción</Label>
                          <Textarea
                            id="descripcion"
                            value={expenseForm.descripcion}
                            onChange={(e) => setExpenseForm(prev => ({ ...prev, descripcion: e.target.value }))}
                            required
                          />
                        </div>
                        <div>
                          <Label htmlFor="monto">Monto</Label>
                          <Input
                            id="monto"
                            type="number"
                            step="0.01"
                            value={expenseForm.monto}
                            onChange={(e) => setExpenseForm(prev => ({ ...prev, monto: parseFloat(e.target.value) || 0 }))}
                            required
                          />
                        </div>
                      </div>
                    )}

                    {expenseForm.tipo && (
                      <div className="flex gap-2">
                        <Button type="submit">
                          {editingExpense ? 'Actualizar' : 'Agregar'} Gasto
                        </Button>
                        {editingExpense && (
                          <Button type="button" variant="outline" onClick={resetExpenseForm}>
                            Cancelar
                          </Button>
                        )}
                      </div>
                    )}
                  </form>
                </CardContent>
              </Card>

              <Card>
                <CardHeader>
                  <CardTitle>Gastos del Proyecto</CardTitle>
                </CardHeader>
                <CardContent>
                  <div className="space-y-2">
                    {selectedProjectData?.expenses.map((expense) => (
                      <div key={expense.id} className="flex items-center justify-between p-3 border rounded-lg">
                        <div>
                          <div className="flex items-center gap-2">
                            <Badge variant="outline">{expense.tipo}</Badge>
                            <span className="font-medium">
                              ${expense.tipo === 'viaticos' 
                                ? (() => {
                                    const details = expense.detalles as any;
                                    const alimentoTotal = Object.values(details.alimento || {}).reduce((sum: number, val: any) => sum + (val || 0), 0);
                                    return ((details.hospedaje || 0) + alimentoTotal).toFixed(2);
                                  })()
                                : expense.monto.toFixed(2)
                              }
                            </span>
                          </div>
                          <div className="text-sm text-muted-foreground">
                            {expense.tipo === 'movilidad' && (
                              <span>{(expense.detalles as any).inicio} → {(expense.detalles as any).destino}</span>
                            )}
                            {expense.tipo === 'viaticos' && (
                              <span>
                                Hospedaje: ${(expense.detalles as any).hospedaje || 0} | 
                                Alimentos: ${Object.values((expense.detalles as any).alimento || {}).reduce((sum: number, val: any) => sum + (val || 0), 0)}
                              </span>
                            )}
                            {expense.tipo === 'compras' && (
                              <span>{(expense.detalles as any).descripcion}</span>
                            )}
                          </div>
                        </div>
                        <div className="flex gap-2">
                          <Button
                            size="sm"
                            variant="outline"
                            onClick={() => handleEditExpense(expense)}
                          >
                            <Edit className="h-4 w-4" />
                          </Button>
                          <Button
                            size="sm"
                            variant="destructive"
                            onClick={() => deleteExpense(selectedProject, expense.id)}
                          >
                            <Trash2 className="h-4 w-4" />
                          </Button>
                        </div>
                      </div>
                    ))}
                  </div>
                </CardContent>
              </Card>
            </>
          )}
        </TabsContent>
      </Tabs>
    </div>
  );
}
