"use client";

import { useProjects } from '@/context/ProjectContext';
import { Card, CardContent, CardHeader, CardTitle } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';
import { Eye, Edit, Trash2 } from 'lucide-react';
import { useState } from 'react';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from '@/components/ui/dialog';
import { Project } from '@/types';

export default function ProjectSummary() {
  const { projects, deleteProject, getProjectSummaries } = useProjects();
  const [selectedProject, setSelectedProject] = useState<Project | null>(null);
  
  const summaries = getProjectSummaries();

  const formatCurrency = (amount: number) => {
    return new Intl.NumberFormat('es-PE', {
      style: 'currency',
      currency: 'PEN'
    }).format(amount);
  };

  const formatDate = (dateString: string) => {
    return new Date(dateString).toLocaleDateString('es-PE');
  };

  const getExpenseDetails = (project: Project) => {
    const expensesByType = {
      movilidad: project.expenses.filter(e => e.tipo === 'movilidad'),
      viaticos: project.expenses.filter(e => e.tipo === 'viaticos'),
      compras: project.expenses.filter(e => e.tipo === 'compras')
    };

    const totals = {
      movilidad: expensesByType.movilidad.reduce((sum, e) => sum + e.monto, 0),
      viaticos: expensesByType.viaticos.reduce((sum, e) => {
        const details = e.detalles as any;
        const alimentoTotal = Object.values(details.alimento || {}).reduce((total: number, val: any) => total + (val || 0), 0);
        return sum + (details.hospedaje || 0) + alimentoTotal;
      }, 0),
      compras: expensesByType.compras.reduce((sum, e) => sum + e.monto, 0)
    };

    return { expensesByType, totals };
  };

  return (
    <div className="space-y-6">
      {summaries.length === 0 ? (
        <Card>
          <CardContent className="flex items-center justify-center py-12">
            <div className="text-center">
              <h3 className="text-lg font-medium text-muted-foreground mb-2">
                No hay proyectos registrados
              </h3>
              <p className="text-sm text-muted-foreground">
                Crea tu primer proyecto en la pestaña de Registro
              </p>
            </div>
          </CardContent>
        </Card>
      ) : (
        <>
          <Card>
            <CardHeader>
              <CardTitle>Resumen General</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
                <div className="text-center p-4 bg-muted rounded-lg">
                  <div className="text-2xl font-bold">{summaries.length}</div>
                  <div className="text-sm text-muted-foreground">Proyectos Totales</div>
                </div>
                <div className="text-center p-4 bg-muted rounded-lg">
                  <div className="text-2xl font-bold">
                    {formatCurrency(summaries.reduce((sum, s) => sum + s.montoTotal, 0))}
                  </div>
                  <div className="text-sm text-muted-foreground">Gasto Total</div>
                </div>
                <div className="text-center p-4 bg-muted rounded-lg">
                  <div className="text-2xl font-bold">
                    {formatCurrency(summaries.reduce((sum, s) => sum + s.montoTotal, 0) / summaries.length)}
                  </div>
                  <div className="text-sm text-muted-foreground">Promedio por Proyecto</div>
                </div>
              </div>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>Lista de Proyectos</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="overflow-x-auto">
                <Table>
                  <TableHeader>
                    <TableRow>
                      <TableHead>SOT</TableHead>
                      <TableHead>Cliente</TableHead>
                      <TableHead>Fecha</TableHead>
                      <TableHead>Lugar</TableHead>
                      <TableHead className="text-right">Monto Total</TableHead>
                      <TableHead className="text-center">Acciones</TableHead>
                    </TableRow>
                  </TableHeader>
                  <TableBody>
                    {summaries.map((summary) => {
                      const project = projects.find(p => p.id === summary.id);
                      return (
                        <TableRow key={summary.id}>
                          <TableCell className="font-medium">{summary.sot}</TableCell>
                          <TableCell>{summary.cliente}</TableCell>
                          <TableCell>{formatDate(summary.fecha)}</TableCell>
                          <TableCell>{summary.lugar}</TableCell>
                          <TableCell className="text-right font-medium">
                            {formatCurrency(summary.montoTotal)}
                          </TableCell>
                          <TableCell className="text-center">
                            <div className="flex justify-center gap-2">
                              <Dialog>
                                <DialogTrigger asChild>
                                  <Button
                                    size="sm"
                                    variant="outline"
                                    onClick={() => setSelectedProject(project || null)}
                                  >
                                    <Eye className="h-4 w-4" />
                                  </Button>
                                </DialogTrigger>
                                <DialogContent className="max-w-4xl max-h-[80vh] overflow-y-auto">
                                  <DialogHeader>
                                    <DialogTitle>
                                      Detalles del Proyecto: {project?.sot} - {project?.cliente}
                                    </DialogTitle>
                                  </DialogHeader>
                                  {project && (
                                    <div className="space-y-6">
                                      <div className="grid grid-cols-2 gap-4">
                                        <div>
                                          <h4 className="font-medium text-sm text-muted-foreground">Personal</h4>
                                          <p>{project.personal}</p>
                                        </div>
                                        <div>
                                          <h4 className="font-medium text-sm text-muted-foreground">Lugar</h4>
                                          <p>{project.lugar}</p>
                                        </div>
                                        <div>
                                          <h4 className="font-medium text-sm text-muted-foreground">Fecha</h4>
                                          <p>{formatDate(project.fecha)}</p>
                                        </div>
                                        <div>
                                          <h4 className="font-medium text-sm text-muted-foreground">Total de Gastos</h4>
                                          <p className="font-bold">{formatCurrency(summary.montoTotal)}</p>
                                        </div>
                                      </div>

                                      {project.expenses.length > 0 && (
                                        <div>
                                          <h4 className="font-medium mb-4">Desglose de Gastos</h4>
                                          <div className="space-y-4">
                                            {(() => {
                                              const { expensesByType, totals } = getExpenseDetails(project);
                                              return (
                                                <>
                                                  {totals.movilidad > 0 && (
                                                    <div>
                                                      <div className="flex items-center justify-between mb-2">
                                                        <h5 className="font-medium flex items-center gap-2">
                                                          <Badge variant="outline">Movilidad</Badge>
                                                        </h5>
                                                        <span className="font-bold">{formatCurrency(totals.movilidad)}</span>
                                                      </div>
                                                      <div className="space-y-1 ml-4">
                                                        {expensesByType.movilidad.map((expense) => {
                                                          const details = expense.detalles as any;
                                                          return (
                                                            <div key={expense.id} className="flex justify-between text-sm">
                                                              <span>{details.inicio} → {details.destino}</span>
                                                              <span>{formatCurrency(expense.monto)}</span>
                                                            </div>
                                                          );
                                                        })}
                                                      </div>
                                                    </div>
                                                  )}

                                                  {totals.viaticos > 0 && (
                                                    <div>
                                                      <div className="flex items-center justify-between mb-2">
                                                        <h5 className="font-medium flex items-center gap-2">
                                                          <Badge variant="outline">Viáticos</Badge>
                                                        </h5>
                                                        <span className="font-bold">{formatCurrency(totals.viaticos)}</span>
                                                      </div>
                                                      <div className="space-y-1 ml-4">
                                                        {expensesByType.viaticos.map((expense) => {
                                                          const details = expense.detalles as any;
                                                          const alimentoTotal = Object.values(details.alimento || {}).reduce((sum: number, val: any) => sum + (val || 0), 0);
                                                          const total = (details.hospedaje || 0) + alimentoTotal;
                                                          return (
                                                            <div key={expense.id} className="text-sm">
                                                              <div className="flex justify-between">
                                                                <span>Total</span>
                                                                <span>{formatCurrency(total)}</span>
                                                              </div>
                                                              {details.hospedaje > 0 && (
                                                                <div className="flex justify-between text-muted-foreground ml-2">
                                                                  <span>• Hospedaje</span>
                                                                  <span>{formatCurrency(details.hospedaje)}</span>
                                                                </div>
                                                              )}
                                                              {alimentoTotal > 0 && (
                                                                <div className="flex justify-between text-muted-foreground ml-2">
                                                                  <span>• Alimentos</span>
                                                                  <span>{formatCurrency(alimentoTotal)}</span>
                                                                </div>
                                                              )}
                                                            </div>
                                                          );
                                                        })}
                                                      </div>
                                                    </div>
                                                  )}

                                                  {totals.compras > 0 && (
                                                    <div>
                                                      <div className="flex items-center justify-between mb-2">
                                                        <h5 className="font-medium flex items-center gap-2">
                                                          <Badge variant="outline">Compras</Badge>
                                                        </h5>
                                                        <span className="font-bold">{formatCurrency(totals.compras)}</span>
                                                      </div>
                                                      <div className="space-y-1 ml-4">
                                                        {expensesByType.compras.map((expense) => {
                                                          const details = expense.detalles as any;
                                                          return (
                                                            <div key={expense.id} className="flex justify-between text-sm">
                                                              <span>{details.descripcion}</span>
                                                              <span>{formatCurrency(expense.monto)}</span>
                                                            </div>
                                                          );
                                                        })}
                                                      </div>
                                                    </div>
                                                  )}
                                                </>
                                              );
                                            })()}
                                          </div>
                                        </div>
                                      )}
                                    </div>
                                  )}
                                </DialogContent>
                              </Dialog>
                              <Button
                                size="sm"
                                variant="destructive"
                                onClick={() => {
                                  if (confirm('¿Estás seguro de que quieres eliminar este proyecto?')) {
                                    deleteProject(summary.id);
                                  }
                                }}
                              >
                                <Trash2 className="h-4 w-4" />
                              </Button>
                            </div>
                          </TableCell>
                        </TableRow>
                      );
                    })}
                  </TableBody>
                </Table>
              </div>
            </CardContent>
          </Card>
        </>
      )}
    </div>
  );
}
